.skills__list {
  display: flex;
  padding: 0px;
  width: 100%;
  list-style: none;
  background-color: #ffffff;
  margin: 0px;
}

.skills__item {
  width: 33%;
  padding: 30px 20px;
  text-align: center;
  box-shadow: -1px 1px 5px rgba(0,0,0,0.5);

}

.skill__title {
  margin-top: 0px;
}

.skills__description {
  transition: .3s;
}

.skills__item {
  outline: none;
}

.skills__item:hover .skills__description,
.skills__item:focus .skills__description {
  transform: scale(1.1, 1.1);
  transition: 0.4s;
  cursor: pointer;
}

.certificate-overlay {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-box-shadow: -40px 0 40px -40px rgba(2,4,6,.75), 40px 0 40px -40px rgba(2,4,6,.75), 0 8px 20px 2px rgba(2,4,6,.2);
  box-shadow: -40px 0 40px -40px rgba(2,4,6,.75), 40px 0 40px -40px rgba(2,4,6,.75), 0 8px 20px 2px rgba(2,4,6,.2);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}

.certificate1,
.certificate2,
.certificate3 {
  display: block;
  transition: 1s;
  margin: auto;
  width: 60vh;
  height: 90vh;
}

.hidden {
  display: none;
}

@media (max-width: 1239px) {
  .certificate1,
  .certificate2,
  .certificate3 {
    width: 500px;
    height: 700px;
  }
}

@media (max-width: 839px) {
  .skills__list {
    flex-direction: column;
  }

  .skills__item {
    width: 100%;
    padding: 30px 0;
  }

  .certificate1,
  .certificate2,
  .certificate3 {
    width: 280px;
    height: 390px;
  }
}
