@font-face {
  font-family: "Rubik-Regular";
  src: url("../fonts/Rubik-Regular.woff2"), url("../fonts/Rubik-Regular.woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Rubik-Medium";
  src: url("../fonts/Rubik-Medium.woff2"), url("../fonts/Rubik-Medium.woff");
  font-weight: normal;
  font-style: normal;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
a {
  outline: none;
}
ul {
  margin: 0;
}
body {
  font-family: "Rubik-Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  min-width: 320px;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
  color: black;
}
.container {
  width: 1200px;
  margin: 0px auto;
  padding: 0px 20px;
}
@media (max-width: 1239px) {
  .container {
    width: 710px;
  }
}
@media (max-width: 839px) {
  .container {
    width: 280px;
  }
}
.animation-greetings {
  display: flex;
  flex-direction: column;
  height: inherit;
}
.animation-header-background {
  background-image: url(../img/jpg/header111.jpg);
  background-repeat: no-repeat;
  height: 700px;
  padding-top: 30px;
  box-shadow: inset 0px -65px 270px #000000;
  background-position-x: 500px;
  opacity: 0;
}
.page-header {
  background-color: #000000;
}
.page-header__wrapper {
  background-image: url(../img/jpg/header111.jpg);
  background-repeat: no-repeat;
  height: 700px;
  padding-top: 30px;
  box-shadow: inset -200px -65px 270px #000000;
  background-position-x: 0px;
  transition: all 0.7s ease-in;
}
.page-header__header-top {
  display: flex;
  justify-content: space-between;
}
.page-header__middle {
  display: flex;
  justify-content: space-between;
  height: inherit;
}
.page-header__greeting-wrapper {
  display: flex;
  flex-direction: column;
  height: 300px;
  margin: auto 0;
  transition: all 0.7s ease-in;
}
.page-header__greeting {
  margin: auto;
  color: #ffffff;
  text-align: center;
}
.page-header__greeting-description {
  color: #ffffff;
  max-width: 487px;
  text-align: center;
  line-height: 28px;
  margin: auto;
}
.page-header__download-cv {
  display: flex;
  width: 130px;
  margin: auto;
  transition: all 0.3s ease-in;
}
.page-header__download-cv:hover,
.page-header__download-cv:focus {
  box-shadow: 0 0 10px #ffffff;
  transition: all 0.3s ease-in;
}
.page-header__download-black::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 40px;
  background-repeat: no-repeat;
  background-image: url(../img/png/arrow.png);
  width: 15px;
  height: 11px;
  transition: all 0.3s ease-out;
}
.page-header__download-cv:hover .page-header__download-black::after,
.page-header__download-cv:focus .page-header__download-black::after {
  left: 45px;
  transition: all 0.3s ease-out;
}
.page-header__download-white {
  padding: 10px 10px 10px 15px;
  background-color: #ffffff;
  font-weight: 600;
}
.page-header__download-black {
  position: relative;
  background-color: #000000;
  color: white;
  padding: 10px 35px 10px 10px;
}
.backToTop {
  position: fixed;
  width: 70px;
  height: 70px;
  background: #000000;
  bottom: 45px;
  right: 45px;
  transition: all 0.5s ease-in;
  opacity: 0;
  border-radius: 50%;
  z-index: 1;
  padding: 15px;
}
.backToTop-wrapper {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  border-radius: 50%;
  padding: 8px;
  height: 53px;
}
.backToTop__arrow {
  display: block;
  width: 33px;
  height: 5px;
  background: black;
  position: absolute;
  top: 44%;
  border-radius: 3px;
}
.backToTop__arrow:first-child {
  transform: rotate(-45deg);
  left: 23px;
}
.backToTop__arrow:nth-child(2) {
  left: 43px;
  transform: rotate(45deg);
}
.backToTop-show {
  opacity: 0.6;
  cursor: pointer;
}
.backToTop-show:hover,
.backToTop-show:focus {
  opacity: 1;
  transition: all 0.3s ease-in;
}
@media (max-width: 1239px) {
  .page-header__wrapper {
    background-position-x: -285px;
  }
}
@media (max-width: 839px) {
  .page-header__wrapper {
    background-image: url(../img/jpg/header11-tablet.jpg);
    background-position-x: -175px;
    box-shadow: inset -10px -65px 270px #000000;
  }
  .page-header__greeting-wrapper {
    padding-top: 270px;
    width: 100%;
  }
  .page-header__middle {
    position: relative;
  }
  .social {
    position: absolute;
    right: 258px;
  }
  .page-header__download-cv {
    border: 1px solid white;
  }
  .backToTop {
    left: 45px;
    height: 55px;
    width: 57px;
  }
  .backToTop-wrapper {
    position: relative;
    height: 39px;
  }
  .backToTop__arrow {
    top: 42%;
  }
  .backToTop__arrow:first-child {
    left: 2px;
  }
  .backToTop__arrow:nth-child(2) {
    left: 22px;
  }
}
.social {
  width: 23px;
  padding-top: 200px;
}
.social__list {
  list-style: none;
  width: inherit;
  padding: 0px;
}
.social__item {
  margin-bottom: 15px;
}
.social__link {
  position: relative;
}
.social__link--git {
  right: 3px;
  top: -2px;
}
.social__link--facebook {
  right: 5px;
}
.social__img {
  position: relative;
  width: 25px;
  height: 25px;
  opacity: 0.3;
  transition: 0.3s;
}
.social__img--instagram {
  top: -2px;
}
.social__img--linkedin {
  left: -2px;
}
.social__img--facebook {
  top: -9px;
}
.social__link--git:hover .social__img--github,
.social__link--linkedin:hover .social__img--linkedin,
.social__link--htmlacademy:hover .social__img--htmlacademy,
.social__link--instagram:hover .social__img--instagram,
.social__link--facebook:hover .social__img--facebook,
.social__link--git:focus .social__img--github,
.social__link--linkedin:focus .social__img--linkedin,
.social__link--htmlacademy:focus .social__img--htmlacademy,
.social__link--instagram:focus .social__img--instagram,
.social__link--facebook:focus .social__img--facebook {
  opacity: 1;
  transition: 0.5s;
}
.social__link--git:active .social__img--github,
.social__link--linkedin:active .social__img--linkedin,
.social__link--htmlacademy:active .social__img--htmlacademy,
.social__link--instagram:active .social__img--instagram,
.social__link--facebook:active .social__img--facebook {
  opacity: 0.5;
  transition: 0.5s;
}
@media (max-width: 1239px) {
  .social {
    padding-top: 45px;
  }
  .social__img {
    opacity: 0.8;
  }
  .social__img:active {
    opacity: 1;
  }
}
@media (max-width: 839px) {
  .social {
    position: relative;
    right: 258px;
  }
}
.main-navigation {
  padding-top: 20px;
  margin-left: auto;
}
.main-navigation__list {
  display: flex;
  color: #ffffff;
  list-style: none;
  padding: 0px;
  margin: 0;
}
.main-navigation__description {
  min-height: inherit;
  opacity: 0.7;
  transition: 0.3s;
}
.main-navigation__link:hover .main-navigation__description {
  opacity: 1;
  transition: all 0.2s;
}
.main-navigation__link:active .main-navigation__description {
  opacity: 0.5;
}
.main-navigation__item:not(:last-child) {
  margin-right: 25px;
}
.main-navigation__link {
  display: block;
  position: relative;
  color: #ffffff;
  min-height: 25px;
}
.main-navigation__link::before {
  width: 0px;
  content: "";
  position: absolute;
  bottom: 0px;
  left: 50%;
  border-bottom: 1px solid white;
  transition: 0.3s;
}
.main-navigation__link:hover::before,
.main-navigation__link:focus::before {
  width: 100%;
  left: 0%;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 1239px) {
  .main-navigation__description {
    opacity: 0.8;
  }
  .main-navigation__description:active {
    opacity: 1;
  }
}
@media (max-width: 839px) {
  .main-navigation {
    margin: auto;
    padding: 0;
    width: 100%;
  }
  .main-navigation__list {
    margin: 0;
    justify-content: space-around;
  }
  .main-navigation__item:not(:last-child) {
    margin: 0;
  }
  .main-navigation__item {
    font-size: 14px;
  }
}
.page-main__linear-wrapper {
  padding: 145px 0px 60px;
  background: linear-gradient(11deg, #ffffff 50%, #000000 50%);
}
@media (max-width: 1239px) {
  .page-main__linear-wrapper {
    padding: 75px 0px;
  }
}
@media (max-width: 839px) {
  .page-main__linear-wrapper {
    padding: 25px 0px;
  }
}
.skills__list {
  display: flex;
  padding: 0px;
  width: 100%;
  list-style: none;
  background-color: #ffffff;
  margin: 0px;
}
.skills__item {
  width: 33%;
  padding: 30px 20px;
  text-align: center;
  box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.5);
}
.skill__title {
  margin-top: 0px;
}
.skills__description {
  transition: 0.3s;
}
.skills__item {
  outline: none;
}
.skills__item:hover .skills__description,
.skills__item:focus .skills__description {
  transform: scale(1.1, 1.1);
  transition: 0.4s;
  cursor: pointer;
}
.certificate-overlay {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-box-shadow: -40px 0 40px -40px rgba(2, 4, 6, 0.75), 40px 0 40px -40px rgba(2, 4, 6, 0.75), 0 8px 20px 2px rgba(2, 4, 6, 0.2);
  box-shadow: -40px 0 40px -40px rgba(2, 4, 6, 0.75), 40px 0 40px -40px rgba(2, 4, 6, 0.75), 0 8px 20px 2px rgba(2, 4, 6, 0.2);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.certificate1,
.certificate2,
.certificate3 {
  display: block;
  transition: 1s;
  margin: auto;
  width: 60vh;
  height: 90vh;
}
.hidden {
  display: none;
}
@media (max-width: 1239px) {
  .certificate1,
  .certificate2,
  .certificate3 {
    width: 500px;
    height: 700px;
  }
}
@media (max-width: 839px) {
  .skills__list {
    flex-direction: column;
  }
  .skills__item {
    width: 100%;
    padding: 30px 0;
  }
  .certificate1,
  .certificate2,
  .certificate3 {
    width: 280px;
    height: 390px;
  }
}
.my-goals__wrapper {
  background: linear-gradient(to right, #000000 40%, #ffffff 40%);
}
.my-goals {
  display: flex;
  padding: 100px 0;
  background: linear-gradient(to right, #000000 38%, #ffffff 38%);
}
.my-goals__skill-table-wrapper {
  min-width: 500px;
  padding: 40px 45px 65px;
  margin-right: 70px;
  background: #ffffff;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}
.my-goals__skills-title {
  margin: 0px;
}
.my-goals__tech-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.my-goals__list {
  list-style: none;
  margin: 35px 0 0 0;
  padding: 0px;
}
.my-goals__tech-name {
  width: 50%;
}
.my-goals__tech-skill {
  width: 30px;
  text-align: right;
}
.my-goals__tech-skill-visual,
.my-goals__tech-skill-visual-already {
  display: block;
  height: 10px;
  border-radius: 5px;
}
.my-goals__tech-skill-visual {
  margin-top: 5px;
  width: 100%;
  background-color: #f7f7f7;
}
.my-goals__tech-skill-visual-already {
  background-color: #9d9d9d;
}
.my-goals__tech-skill-animation-html,
.my-goals__tech-skill-animation-css,
.my-goals__tech-skill-animation-js,
.my-goals__tech-skill-animation-aj,
.my-goals__tech-skill-animation-pp {
  width: 1%;
}
.my-goals__tech-skill-visual-already--html {
  width: 99%;
  transition: 2s ease-out;
}
.my-goals__tech-skill-visual-already--css {
  width: 95%;
  transition: 2s ease-out;
}
.my-goals__tech-skill-visual-already--js {
  width: 65%;
  transition: 2s ease-in-out;
}
.my-goals__tech-skill-visual-already--ajax {
  width: 95%;
  transition: 2s ease-in-out;
}
.my-goals__tech-skill-visual-already--pp {
  width: 99%;
  transition: 2s ease-out;
}
.my-goals__item:not(:last-child) {
  margin-bottom: 30px;
}
.my-goals__description-block {
  width: 590px;
  padding: 70px 0;
}
.my-goals__desc-title {
  margin: 0px;
}
.my-goals__description {
  font-size: 16px;
  line-height: 26px;
  margin: 25px 0;
}
.my-goals__button {
  display: flex;
  width: 200px;
  transition: 0.3s;
}
.my-goals__button-black {
  display: block;
  color: #ffffff;
  background: #000000;
  padding: 10px 10px 10px 15px;
  font-weight: 600;
}
.my-goals__button-white {
  display: block;
  position: relative;
  color: #000000;
  background: #ffffff;
  padding: 10px 10px 10px 20px;
  font-weight: 600;
}
.my-goals__button-white::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 70px;
  background-repeat: no-repeat;
  background-image: url(../img/png/black-arrow.png);
  width: 15px;
  height: 11px;
  transition: 0.3s;
}
.my-goals__button:hover,
.my-goals__button:focus {
  box-shadow: 0 0 10px #000000;
  transition: 0.3s;
}
.my-goals__button:hover .my-goals__button-white::after,
.my-goals__button:focus .my-goals__button-white::after {
  left: 90px;
  transition: 0.3s;
}
.my-goals__tech-skill {
  width: 30px;
  height: 30px;
  padding: 10px 5px 0px 4px;
  border-radius: 50%;
  background: #f7f7f7;
}
@media (max-width: 1239px) {
  .my-goals__skill-table-wrapper {
    min-width: 350px;
  }
  .my-goals__description-block {
    padding: 0;
  }
  .my-goals {
    background: none;
    padding: 30px 0;
  }
  .my-goals__skill-table-wrapper {
    min-width: 40%;
    margin-right: 5%;
  }
}
@media (max-width: 839px) {
  .my-goals__wrapper {
    background: #000000;
  }
  .my-goals {
    flex-direction: column;
  }
  .my-goals__skill-table-wrapper {
    margin-right: 0;
  }
  .my-goals__description-block {
    width: 100%;
    margin-top: 50px;
    color: #ffffff;
  }
  .my-goals__button {
